	module.exports = {"data":{"markdownRemark":{"html":"<p>凌晨读《曾国藩》第二部的一些章节，没有读完。昨天读完了第一部。据说这部书在京城里有人喜欢。这是一部历史小说，基本的意义在于曾氏如何从乱世中成长起来的过程，同时也描写了乱世的政治和军事力量的对比。对中国过去官场中的描写，对官场中权力斗争的描写，也属深刻。只是对象是太平军，这样就不能不把太平军当做一种陪衬了。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0081.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0081.md","frontmatter":{"title":"4月25日 星期一"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0081.md absPath of file >>> MarkdownRemark"}}

